<template>
  <div class="larkPlayer">
    <div id="container"></div>
    <button @click="btnclick()">切换标清m3u8</button>
    <button @click="btnclick1()">切换map4</button>
    <div v-text="naoTime"></div>
  </div>
</template>

<script>
import larkplayer from 'larkplayer'
import 'larkplayer-ui'
import 'larkplayer-hls'
// import '../plugins/hello'
// import '../plugins/pause'

export default {
  data () {
    return {
      player: null,
      naoTime: ''
    }
  },
  mounted () {
    if (!this.player) {
      this.init()
    }
  },
  methods: {
    init () {
      const _this = this
      let width = Math.min(document.body.clientWidth, 640)
      _this.$nextTick(() => {
        _this.player = larkplayer('container', {
          width: width,
          height: width * 9 / 16,
          controls: true,
          autoplay: true,
          playsinline: false,
          // src: 'https://baikebcs.bdimg.com/baike-other/big-buck-bunny.mp4'
          source: [{
            src: 'https://baikebcs.bdimg.com/baike-other/big-buck-bunny.mp4',
            type: 'video/mp4'
          }
          // ,{
          //   src: 'https://look.chinahrt.com.cn/courseyun/rx/transcode/20206/317690bd-f600-43a0-87a7-6c6182da4607/283003-m3u8.m3u8',
          //   type: 'application/x-mpegURL'
          // },
          // {
          //   src: 'https://look.chinahrt.com.cn/u/courseyun/7d1e9d587bc648cbbdb869c1300967f8/transcode/20216/5cf04cc0-2d74-4ae2-ab10-3ee59d4388a5/283006-mp4.mp4',
          //   type: 'video/mp4'
          // }
          ],
          UI: {
            // 插件名称默认为首字母小写的类名
            // 可通过 Component.register(ErrorDialog, {name: 'another_name'}) 指定名称
            errorDialog: {}
          }
          // ,
          // plugin: {
          //   hello: {}
          // }
        }, function () {
          console.log('player is ready')
          _this.player.play()
        })
        let timeNow = 0
        let isSeek = false
        let startT = 0
        _this.player.on('timeupdate', function (e) {
        // console.log(33,player.currentTime());
          if (!isSeek) {
            timeNow = _this.player.currentTime()
          }
        })

        _this.player.on('seeking', function (e) {
          isSeek = true
          startT = timeNow
          console.log(33, _this.player.currentTime(), startT)
        })
        _this.player.on('seeked', function (e) {
          isSeek = false
          if (_this.player.currentTime() - timeNow > 2) {
            // alert('快进了')
            _this.naoTime = '当前时间：' + timeNow
            _this.player.currentTime(timeNow)
          }
          console.log(55, _this.player.currentTime())
        })
      })
    },
    btnclick () {
      this.player.src('https://look.chinahrt.com.cn/courseyun/rx/transcode/20206/317690bd-f600-43a0-87a7-6c6182da4607/283003-m3u8.m3u8')
      this.player.play()
    },

    btnclick1 () {
      this.player.src('https://look.chinahrt.com.cn/u/courseyun/7d1e9d587bc648cbbdb869c1300967f8/transcode/20216/5cf04cc0-2d74-4ae2-ab10-3ee59d4388a5/283006-mp4.mp4')
      this.player.play()
    }
  }
}
</script>

<style>

</style>



